<template>
	<view class="content">
		<!-- #ifdef APP-PLUS -->
		<view v-for="(item, index) in list" :key="index">
			<button @click="launchApp(item)">打开{{ item.name }}</button>
		</view>
		<!-- #endif -->
		<view>当前经度{{ count }}：{{ longitude }}</view>
		<view>当前纬度{{ count }}：{{ latitude }}</view>

		<navigator url="../../subpages/test">跳转分包页面</navigator>
		<u-button type="primary" text="检查更新" @click="checkIsUpdate"></u-button>
	</view>
</template>

<script>
import { checkUpdate } from '@/utils/app-update-check.js';

export default {
	data() {
		return {
			count: 1,
			longitude: '',
			latitude: '',
			title: 'Hello',
			list: [
				{
					name: '支付宝',
					pname: 'com.eg.android.AlipayGphone',
					scheme: 'alipay://'
				},
				{
					name: '淘宝',
					pname: 'com.taobao.taobao',
					scheme: 'taobao://'
				},
				{
					name: 'QQ',
					pname: 'com.tencent.mobileqq',
					scheme: 'mqq://'
				},
				{
					name: '微信',
					pname: 'com.tencent.mm',
					scheme: 'weixin://'
				},
				{
					name: '京东',
					pname: 'com.jingdong.app.mall',
					scheme: 'openApp.jdMobile://'
				},
				{
					name: '新浪微博',
					pname: 'com.sina.weibo',
					scheme: 'sinaweibo://'
				},
				{
					name: '今日头条',
					pname: 'com.ss.android.article.news',
					scheme: 'snssdk141://'
				}
			]
		};
	},

	onLoad() {
		this.getAppInfo(0);
		// #ifdef APP-PLUS
		// 获取是否开启定位
		let system = uni.getSystemInfoSync(); // 获取系统信息
		if (system.platform === 'android') {
			// 判断平台
			var context = plus.android.importClass('android.content.Context');
			var locationManager = plus.android.importClass('android.location.LocationManager');
			var main = plus.android.runtimeMainActivity();
			var mainSvr = main.getSystemService(context.LOCATION_SERVICE);
			if (!mainSvr.isProviderEnabled(locationManager.GPS_PROVIDER)) {
				var main = plus.android.runtimeMainActivity();
				var Intent = plus.android.importClass('android.content.Intent');
				var Settings = plus.android.importClass('android.provider.Settings');
				var intent = new Intent(Settings.ACTION_LOCATION_SOURCE_SETTINGS);
				main.startActivity(intent); // 打开系统设置GPS服务页面
			}
		} else if (system.platform === 'ios') {
			var cllocationManger = plus.ios.import('CLLocationManager');
			var enable = cllocationManger.locationServicesEnabled();
			var status = cllocationManger.authorizationStatus();
			plus.ios.deleteObject(cllocationManger);
			console.log('手机系统的定位没有打开');
			uni.showModal({
				title: '提示',
				content: '请打开定位服务功能',
				showCancel: false, // 不显示取消按钮
				success() {
					var UIApplication = plus.ios.import('UIApplication');
					var application2 = UIApplication.sharedApplication();
					var NSURL2 = plus.ios.import('NSURL');
					var setting2 = NSURL2.URLWithString('App-Prefs:root=Privacy&path=LOCATION');
					application2.openURL(setting2);
					plus.ios.deleteObject(setting2);
					plus.ios.deleteObject(NSURL2);
					plus.ios.deleteObject(application2);
				}
			});
		}
		// #endif

		this.getLocation();
	},
	onPullDownRefresh() {
		this.getLocation();
		//刷新数据之后停止刷新效果
		uni.stopPullDownRefresh();
	},

	methods: {
		checkIsUpdate() {
			this.getAppInfo(1);
		},
		// 获取定位
		getLocation() {
			let that = this;
			uni.getLocation({
				type: 'wgs84',
				success: res => {
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
					console.log('当前位置的速度：' + res.speed);
					console.log('当前位置的精确度：' + res.accuracy);
					that.longitude = res.longitude;
					that.latitude = res.latitude;
				}
			});
		},
		// 打开其他APP
		launchApp(e) {
			let _this = this;
			// 判断平台
			// #ifdef APP-PLUS
			if (plus.os.name == 'Android') {
				plus.runtime.launchApplication(
					{
						pname: e.pname
					},
					function(e) {
						_this.$u.toast('未发现该应用');
						console.log('Open system default browser failed: ' + e.message);
					}
				);
			} else if (plus.os.name == 'iOS') {
				plus.runtime.launchApplication(
					{
						action: e.scheme
					},
					function(e) {
						console.log('Open system default browser failed: ' + e.message);
					}
				);
			}
			// #endif
		},
		// 获取线上APP版本信息
		getAppInfo(type) {
			const system_info = uni.getSystemInfoSync();
			let params = {
				os: system_info.platform //本机设备操作系统  （android || ios）
			};
			if (params.os != 'ios' && params.os != 'android') false; //如果不是安卓或ios 返回false

			//这里自行请求API获取版本信息 建议传入操作系统标识，返回本机对应的操作系统最新版本信息，也就是安卓的返回就是安卓的版本信息  ios返回就是ios的版本信息

			//请求获取最新版本
			setTimeout(() => {
				let update_info = {
					version: '1.0.2', //线上版本
					now_url: 'https://dldir1.qq.com/weixin/android/weixin802android1860_arm64.apk', //更新链接
					silent: 0, //是否是静默更新
					force: 0, //是否是强制更新
					net_check: 1, //非WIfi是否提示
					note: '<p>1.要改这个字的颜色<p><p>2.需要更新更新呵呵哈哈哈</p>' //更新内容
				};
				//检查更新
				checkUpdate(update_info, type).then(res => {
					if (res.msg) {
						alert(11111111);
						plus.nativeUI.toast(res.msg);
					}
				});
			}, 200);
		}
	}
};
</script>
<style lang="scss" scoped></style>
